<template>
	<view>
		<u-form :model="dataForm" ref="uForm2">
			<u-form-item label="股票代码" prop="code" label-width="150rpx">
				<u-input v-model="dataForm.code" focus placeholder="请输入股票代码" />
			</u-form-item>
		</u-form>
		<u-button style="margin: 20rpx 40rpx 0;" @click="queryHandle()" :loading="isLoading" loadingText="加载中">查询</u-button>
	</view>
	
</template>
<script>
	export default {
		data() {
			return {
				list:[],
				isLoading: false,
				dataForm: {
					// code:''
				},
				values:'',
				rules: {
					code: [{
						required: true,
						message: '股票代码不能为空',
						trigger: 'blur',
					}],
				}
			}
		},
		computed:{

		},
		watch:{
			 "dataForm.code":{
			      handler(val,oldVal){
					  this.dataForm.code = val.toUpperCase()
				  }
			    },
		},
		onReady() {
			this.$refs.uForm2.setRules(this.rules);
		},
		onLoad() {
			
		},
		methods: {
			queryHandle() {
				this.$refs.uForm2.validate(valid => {
					if (valid) {
						uni.navigateTo({
							url: `/pages/access/access-ztjcfw/utweb-result?code=${this.dataForm.code}`
						})
					}
				});
			}
		}
	}
</script>
<style scoped>
	.chart-tooltip-div {
		font-size: 12px;
		line-height: 1.3;
		color: #333;
		border: 1px solid #4AC2F9;
	}
	
	.chart-tooltip-div .mai {
		display: flex;
		align-items: center;
		border-bottom: 1px solid #4AC2F9;
	}
	
	.chart-tooltip-div .mai .lefts {
		width: 34px;
		margin: 0 auto;
		font-size: 14px;
		line-height: 30px;
		height: 100%;
		overflow: hidden;
		white-space: pre-wrap;
		padding: 0 10px;
	}
	
	.chart-tooltip-div .mai .rights {
		width: calc(100% - 34px);
		padding: 5px;
		border-left: 1px solid #4AC2F9;
	}
	
	.chart-tooltip-div .mai .rights span {
		padding: 0 10px;
		line-height: 20px;
	}
	
	.chart-tooltip-div .bottom {
		padding: 5px 0;
		line-height: 20px;
	}
	
	.chart-tooltip-div .bottom .flex {
		display: flex;
	}
	
	.chart-tooltip-div .bottom .flex div {
		flex: 1;
		padding:0 5px;
	}
	
	.chart-tooltip-div .bottom .flex .lefts{
		width:60px;
		text-align:right;
	}
	.chart-tooltip-div .bottom .flex .rights{
		width:calc(100% - 60px);
		
	}
	.chart-tooltip-div .bottom span {
		line-height: 20px;
	}
</style>
